import { newArticle } from '@/api/article'

export default {
  // 在路由匹配组件渲染之前会先执行中间件原理
  middleware: 'authenticated',
  name: 'EditorIndex',
  data () {
    return {
      article: {
        title: '',
        description: '',
        body: '',
        tagList: ''
      }
    }
  },
  methods: {
    async onSubmit () {
      console.log(111);
      try {
        const { data } = await newArticle({
          article: this.article
        })

        this.$router.push('/')
      } catch (err) {

      }

    }
  }
}
